html,
body {
    height: 100%;
    font-family: 'Cormorant Garamond';
}
.main-page {
    background: #FFF;
}

button {
    display: flex;
    padding: 1vw 1vw;
    justify-content: center;
    align-items: center;
    gap: 0.5vw;
    flex-shrink: 0;
    border-radius: 1.5vw;
    border: 0.3vw solid #78424A;
    background: none;
    font-family:'Cormorant Garamond';
    cursor: pointer;
    color: var(--black, #393939);
    font-size: 2vw;
    font-weight: 400;
    line-height: 2vw;
    box-sizing: border-box;
    text-transform: uppercase;
}
button:hover {
    background-color: #78424A;
    color: #fff;
    display: flex;
    flex-direction: row;
    transition-duration: 0.5s;
}
button:hover .material-symbols-outlined{
    color:#fff;
    transition-duration: 0.5s;
}
.white-button,
.white-text {
    color: #fff;
}
.white-arrow {
    color: #fff !important;
    font-size: 2vw;
}
hr {
    height: 0.3vw;
    margin: 0 3%;
    border: none;
    background-color: #5E2129;
}
a {
    color: var(--black, #393939);
    font-size: 1.7vw;
    font-weight: 400;
    line-height: 1.7vw;
}
.inline-input {
    padding: 1vw 1vw;
    border-radius: 1.5vw 0 0 1.5vw;
    flex-shrink: 0;
    border: 0.3vw solid #78424A;
    color: var(--black, #393939);
    font-family: Cormorant Garamond;
    font-size: 2vw;
    font-weight: 400;
    line-height: 2vw;
    box-sizing: border-box;
    width: 70%;
}
.inline-input-submit-btn {
    left: -5px;
    display: flex;
    padding: 1vw 1vw;
    justify-content: center;
    align-items: center;
    gap: 1vw;
    flex-shrink: 0;
    border-radius: 0 1.5vw 1.5vw 0;
    border: 0.3vw solid #78424A;
    background: none;
    font-family:'Cormorant Garamond';
    cursor: pointer;
    color: var(--black, #393939);
    font-size: 2vw;
    font-weight: 400;
    line-height: 2vw; 
    /*height: 5.5vw;*/
    box-sizing: border-box;
    width: 30%;
}
.inline-input-submit-btn:hover {
    border: 3px solid #78424A;
    background-color: #78424A;
    color: white;
    transition-duration: 0.5s;
}

/*header*/
.main-page-logo {
    cursor: pointer;
    position: absolute;
    width: 20%;
    margin-left: 50%;
    transform: translate(-90%, 0%);
}
.main-page-header-navigation {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    box-sizing: border-box;
    width: 25%;
}
.main-page-header-navigation-option {
    display: flex;
    align-items: center;
    font-size: 2vw;
    position: relative;
    line-height: 2vw;
    text-decoration: none;
    margin: 0;
    text-transform: uppercase;
}
.main-page-header-navigation .main-page-header-navigation-option::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0.1em;
    background-color: #393939;
    opacity: 0;
    transition: opacity 300ms, transform 300ms;
}
.main-page-header-navigation .main-page-header-navigation-option:hover::after,
.main-page-header-navigation .main-page-header-navigation-option:focus::after {
  opacity: 1;
  transform: translate3d(0, 0.2em, 0);
}
.main-page-header-navigation .main-page-header-navigation-option::after {
    opacity: 1;
    transform: scale(0);
    transform-origin: center;
}
  
.main-page-header-navigation .main-page-header-navigation-option:hover::after,
.main-page-header-navigation .main-page-header-navigation-option:focus::after {
transform: scale(1);
}
.main-page-header-icons {  
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    gap: 3vw;
}
.header-icon {
    font-size: 3vw;
    cursor: pointer;
}
header {
    position: relative;
    box-sizing: border-box;
    height: 9vw;
    padding: 2vw 4% 2vw 6%;
    flex-shrink: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
}

/* themed products block */
.main-page-themed-products-block-container {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding: 4% 4% 8% 4%;
}
.themed-products-heading {
    margin: 0;
    color: var(--black, #393939);
    font-size: 4vw;
    font-weight: 600;
    line-height: normal;
    text-align: center;
}
.products-container {
    margin-top: 3%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 6vw;
}
.product {
    position: relative;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}
.product:hover .product-add-to-cart-button {
    display: flex;
    bottom: 11vw;
}
.product img {
    margin: 0;
    width: 100%; 
}
.product-add-to-cart-button {
    margin: 0;
    width: 100%;
    padding: 0.5vw 1vw;
    position: absolute;
    border: none;
    border-radius: 0;
    background: #78424A;
    color: var(--white, #FFF);
    font-size: 1.5vw;
    gap: 1vw;  
    opacity: 50%;
    display: none;
}
.product-add-to-cart-button:hover {
    opacity: 100%;
}
.product-info {
    box-sizing: content-box;
    height: 11vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.product-description {
    margin: 2vw 0 0 0;
    text-align: center;
    color: var(--black, #393939);
    font-size: 2vw;
    font-weight: 400;
    line-height: 2vw;
    cursor: pointer;
}
.product-price {
    text-align: center;
    margin: 0px;
    color: var(--black, #393939);
    font-size: 3vw;
    font-weight: 700;
    line-height: 2.5vw;
    cursor: pointer;
}
.product-add-to-cart-button-input-label {
    margin: 0;
    height: 100%; 
    cursor: pointer;
}
#checkbox1 {
    display: none;
}
#checkbox1:checked ~ .fa-heart-o{
    display: none;
}
#checkbox1:checked ~ .fa-heart {
    display: inline;
}
#checkbox1 ~ .fa-heart-o {
    display: inline;
}
#checkbox1 ~ .fa-heart {
    display: none;
}

#checkbox2 {
    display: none;
}
#checkbox2:checked ~ .fa-heart-o {
    display: none;
}
#checkbox2:checked ~ .fa-heart {
    display: inline;
}
#checkbox2 ~ .fa-heart-o {
    display: inline;
}
#checkbox2 ~ .fa-heart {
    display: none;
}

#checkbox3 {
    display: none;
}
#checkbox3:checked ~ .fa-heart-o {
    display: none;
}
#checkbox3:checked ~ .fa-heart {
    display: inline;
}
#checkbox3 ~ .fa-heart-o {
    display: inline;
}
#checkbox3 ~ .fa-heart {
    display: none;
}

.products-additional-block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.products-additional-block-text {
    color: var(--black, #393939);
    text-align: center;
    font-size: 2vw;
    font-weight: 400;
    line-height: 2vw;
    margin: 0 0 3vw 0;
}
.products-additional-block-button {
    font-size: 2vw;
    line-height: 2vw;
}


/*categories block*/
.main-page-categories-block-container {
    box-sizing: border-box;
    text-align: center;
    padding: 4vw 6%;
    flex-shrink: 0;
    background: rgba(120, 66, 74, 0.50);
}
.main-page-categories-block-heading {
    color: var(--black, #393939);
    margin: 0px;
    font-size: 5vw;
    font-weight: 600;
    line-height: 5vw;
}
.main-page-categories-block-items {
    text-align: center;
    margin-top: 4vw;
    display: flex;
    justify-content: space-between;
}
.category-item {
    box-sizing: border-box;
    display: flex;
    width: 15vw;
    flex-direction: column;
}
.main-page-categories-item {
    width: 100%;
}
.category-item-name {
    text-align: center;
    font-size: 2vw;
    font-weight: 400;
    line-height: 2vw;
    background: #5E2129;
    box-sizing: border-box;
    padding: 1vw 1vw;
    margin: 0;
    cursor: pointer;
    text-transform: uppercase;
}
.category-item-name:hover {
    background-color: #78424A;
}

/*quiz-block*/
.main-page-quiz-block-container {
    box-sizing: border-box;
    position: relative;
    margin: 6% 0;
    padding: 0 3%;
}
.main-page-quiz-img {
    width: 100%; 
    height: auto;
}
.quiz-block-button {
    position: absolute;
    top: 70%;
    left: 64%;
    padding: 2vw 1vw;
}

/*footer*/
footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 5% 6%;
    box-sizing: border-box;
}
.footer-categories-block {
    width: 60%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    box-sizing: border-box;
}
.footer-category-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    box-sizing: border-box;
    width: 20vw;
}
.footer-category-name {
    margin: 0 0 4vw 0;
    padding: 1.5vw 2.5vw;
    border: 0.3vw solid #78424A;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-shrink: 0;
    color: var(--black, #393939);
    text-align: center;
    font-size: 2vw;
    font-weight: 400;
    line-height: 2vw;
    text-transform: uppercase;
}
.footer-navigation {
    display: flex;
    flex-direction: column;
    gap: 2vw;
}
.footer-navigation a {
    text-transform: uppercase;
}
.footer-authentication-n-contacts-container {
    width: 40%;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
}
.footer-authentication-heading {
    margin: 0;
    color: var(--main, #78424A);
    font-size: 4vw;
    font-weight: 700;
    line-height: normal;
    text-transform: uppercase;
}
.footer-authentication-info {
    margin: 2vw 0 2vw 0; 
    color: var(--black, #393939);
    font-size: 2.5vw;
    font-weight: 400;
    line-height: 2.5vw;
}
.footer-form {
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    width: 100%;
}
.footer-sign-up-input {
    height: 5.5vw;
}
.footer-sign-up-submit-btn {
    height: 5.5vw;
}

.footer-contacts {
    margin: 0px;
    color: var(--black, #393939);
    font-size: 2.5vw;
    font-weight: 400;
    line-height: 2.5vw;
    margin: 3vw 0;
    text-transform: uppercase;
}
.footer-share-icons {
    display: flex;
    gap: 3vw;
    height: 3vw;
}
.fa-2xl {
    color: #393939; 
    cursor: pointer;
    line-height: 1 !important;
    font-size: 3vw;
}
